import React, { Component } from 'react'

export class Header extends Component {
    render() {
        return (
            <div>
                <h1 className = "display-4 py-4">Demo Project to show the Working of GitHub.</h1>
                <h4 className = "blockquote py-4">GitHub is an online repository management system which helps people to host their software development with version control inbuilt within git. It offers distributed version control and source code management functionality. It also has some features such as bug tracking, feature request, task management, continuous integration and branching techniques.</h4>
            </div>
        )
    }
}

export default Header;