import React, { Component } from 'react'

const Context = React.createContext();

const reducer = (state,action) =>{
    switch(action.type){
        case 'DELETE_CONTACT':
            return{
                ...state,
                contacts: state.contacts.filter(contact => contact.id !== action.payload )
            }
        default:
            return state;
    }
}
export class Provider extends Component {
    state = {
        contacts:[
                {
                    id:1,
                    name: 'Santhosh Kumar D',
                    email: 'santhoshkumar.d2017@vitstudent.ac.in',
                    regno: '17MIS0146'
                },
                {
                    id:2,
                    name: 'D U Suriya',
                    email: 'du.suriya2017@vitstudent.ac.in',
                    regno: '17MIS0435'
                },
                {
                    id:3,
                    name: 'Deiveegaraman V',
                    email: 'deiveegaraaman.2017@vitstudent.ac.in',
                    regno: '17MIS0477'
                },
                {
                    id:4,
                    name: 'Gautham Shankar S',
                    email: 'gautham.shankar2017@vitstudent.ac.in',
                    regno: '17MIS0479'
                }
            ],
            dispatch : action => this.setState(state => reducer(state,action))
    }
    render() {
        return (
            <Context.Provider value = {this.state}>
                {this.props.children}
            </Context.Provider>
        )
    }
} 

export const Consumer = Context.Consumer;
